ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

.button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border-radius: 5px;
  height: 40px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__logo-img {
  width: 100%;
}

.menu__list {
  gap: 20px;
  display: flex;
}

.menu__item {
  display: flex;
}

.menu__link {
  color: #060b35;
  text-transform: uppercase;
  padding-block: 22.5px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.25;
  transition: color .3s ease-in;
  position: relative;
}

.menu__link:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 0%;
  height: 4px;
  transition: width .5s ease-out;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu__link:hover {
  color: #00acdc;
}

.menu__link:hover:after {
  width: 100%;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: width .5s ease-out;
  position: absolute;
  bottom: 0;
  left: 0;
}

.shop__cards {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .shop__cards {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .shop__cards {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .shop__cards {
    grid-template-columns: repeat(4, 200px);
  }
}

.shop__card:nth-child(n+4) {
  margin-right: 0;
}

.card {
  cursor: pointer;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  margin-bottom: 40px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: flex;
}

.card__name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  transition: all .3s;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
}

.card__reiting {
  justify-content: space-between;
  align-items: center;
