}

.card__stars--1 :nth-child(-n+1), .card__stars--2 :nth-child(-n+2), .card__stars--3 :nth-child(-n+3), .card__stars--4 :nth-child(-n+4), .card__stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
}

.card__cost {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.card__price-range {
  color: #060b35;
  font-size: 16px;
